.search__container {
    height: 75px;
    display: flex;
    align-items: center;
    justify-content: left;
    margin-left: 20px;
}

.search {
    height: 50px;
    position: relative;
}

.search__input {
    background-color: #fff;
    border: 0;
    font-size: 14px;
    height: 50px;
    width: 50px;
    transition: width 0.3s ease;
}

.search__button {
    background-color: #fff;
    border: 0;
    cursor: pointer;
    font-size: 24px;
    position: absolute;
    left: 0;
    height: 50px;
    width: 50px;
    transition: transform 0.3s ease;
}

.search__button, .search__input:focus {
    outline: none;
}

.search.active .search__input {
    width: 200px;
}

.search.active .search__button {
    transform: translateX(198px);
}



